import React from 'react';
import s from './MyPosts.module.css';
import { Row, Col, Container } from 'react-bootstrap';
import Post from './Post/Post';
import {reduxForm, Field} from 'redux-form';
import {required, maxLengthCreator} from './../../../utils/validators/validators';
import {Textarea} from '../../common/FormsControls/FormsControls';

const maxLength10 = maxLengthCreator(10);

const AddPostForm = (props) => {
    return(
        <form className={s.postForm} onSubmit={props.handleSubmit}>
                    <Field component={Textarea} name="post" id="" cols="30" rows="10" placeholder="Write your activity" 
                    validate={[required, maxLength10]}/>
                    <button className={s.commentPost}>Add Post</button>
                </form>
    )
}

const AddPostFormRedux = reduxForm({form: 'addPostForm'})(AddPostForm)
window.props = [];

const MyPosts = React.memo(props => {

        let postsElement = 
        props.profilePage.map(p => <Post id={p.id} postText={p.postText} likes={p.likes} posts={props.posts}/>)

        let addPost = (values) => {
            debugger;
            props.addPost(values.post)
        }
        console.log("reder")
        return(
            <div>
            <Container fluid className={s.commentContainer}>
            <Row className={s.commentRow}>
                <Col lg={1} xs={12}><img className={s.userMini} src="https://i.pinimg.com/originals/bb/e2/1f/bbe21faf9642c04f811f2f87893fc174.jpg" alt="user-circle"/></Col>
                <Col lg={11} xs={12}>
                <AddPostFormRedux onSubmit={addPost}/>    
                </Col>
            </Row>
            </Container>
                {postsElement}
            </div>
        )
    })

export default MyPosts;