import React from 'react';
import Profile from "./Profile";
import {setUserProfile} from '../../redux/profileReducer';
import {connect} from 'react-redux';
import { withRouter } from 'react-router-dom';
import {getUserProfileThunkCreator, getStatusThunkCreator, updateStatusThunkCreator} from '../../redux/profileReducer';
import { compose } from 'redux';

class ProfileContainer extends React.Component{
    componentDidMount(){
        let userId = this.props.match.params.userId;
        if(!userId){
            userId = this.props.authorizedUserId;
            if(!userId){
                this.props.history.push('/login');
            }
        }
        this.props.getUserProfileThunkCreator(userId);
        this.props.getStatusThunkCreator(userId);
    }

    render(){
        return(
        <Profile {...this.props} profile={this.props.profile} status={this.props.status} 
        updateStatusThunkCreator={this.props.updateStatusThunkCreator}/>
        )
    }
}

let mapStateToProps = (state) => ({
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    authorizedUserId: state.auth.userId,
    isAuth: state.auth.isAuth
})


export default compose(
    connect (mapStateToProps, {setUserProfile, getUserProfileThunkCreator, getStatusThunkCreator, updateStatusThunkCreator}),
    withRouter
    // withAuthRedirect
)(ProfileContainer);

